
import { db } from './firebase';
import { collection, getDocs, getDoc, doc, setDoc, deleteDoc, query, where, writeBatch, updateDoc, addDoc, orderBy, limit, startAfter, documentId } from 'firebase/firestore';
import type { Vehicle, GalleryImage, VehicleDataInput } from './types';
import { unstable_noStore as noStore } from 'next/cache';
import { errorEmitter } from '@/firebase/error-emitter';
import { FirestorePermissionError } from '@/firebase/errors';

// NOTE: Using unstable_noStore() helps in development to see changes, 
// but for production, you might want a more sophisticated caching strategy.

export async function fetchVehicles(filters?: { type?: string, price?: string }): Promise<Vehicle[]> {
    noStore();
    try {
        const vehiclesCollection = collection(db, 'vehicles');
        let q;
        
        const typeFilter = filters?.type && filters.type !== 'all' 
            ? where('type', '==', filters.type)
            : null;

        if (typeFilter) {
            q = query(vehiclesCollection, typeFilter, orderBy('id'));
        } else {
            q = query(vehiclesCollection, orderBy('id'));
        }

        const querySnapshot = await getDocs(q);
        let vehicles = querySnapshot.docs.map(doc => {
            const data = doc.data();
            return {
                id: parseInt(doc.id),
                make: data.make,
                model: data.model,
                price: data.price,
                description: data.description,
                type: data.type,
                imageUrl: data.imageUrl,
            } as Vehicle;
        });

        if (filters?.price && filters.price !== 'all') {
            const [min, max] = filters.price.split('-').map(Number);
            vehicles = vehicles.filter(v => {
                if (max) {
                    return v.price >= min && v.price <= max;
                }
                return v.price >= min;
            });
        }
        
        return vehicles;
    } catch (error: any) {
        if (error.code === 'permission-denied') {
             const permissionError = new FirestorePermissionError({
                path: 'vehicles',
                operation: 'list',
            });
            errorEmitter.emit('permission-error', permissionError);
        }
        console.error("Error fetching vehicles:", error);
        throw error;
    }
}

export async function fetchVehicleById(id: string): Promise<Vehicle | null> {
    noStore();
    try {
        const vehicleDocRef = doc(db, 'vehicles', id);
        const vehicleDoc = await getDoc(vehicleDocRef);

        if (vehicleDoc.exists()) {
            const data = vehicleDoc.data();
            return {
                id: parseInt(vehicleDoc.id),
                make: data.make,
                model: data.model,
                price: data.price,
                description: data.description,
                type: data.type,
                imageUrl: data.imageUrl,
            } as Vehicle;
        } else {
            return null;
        }
    } catch (error: any) {
        if (error.code === 'permission-denied') {
             const permissionError = new FirestorePermissionError({
                path: `vehicles/${id}`,
                operation: 'get',
            });
            errorEmitter.emit('permission-error', permissionError);
        }
        console.error("Error fetching vehicle by ID:", error);
        throw error;
    }
}

export async function fetchAllVehicleIds() {
    noStore();
    try {
        const vehiclesCollection = collection(db, 'vehicles');
        const vehicleSnapshot = await getDocs(query(vehiclesCollection, orderBy(documentId())));
        return vehicleSnapshot.docs.map(doc => ({ id: doc.id }));
    } catch (error) {
        console.error("Error fetching vehicle IDs:", error);
        return [];
    }
}

export async function fetchFeaturedVehicles(count = 4) {
    noStore();
    try {
        const vehiclesCollection = collection(db, 'vehicles');
        const q = query(vehiclesCollection, orderBy('price', 'desc'), limit(count));
        const querySnapshot = await getDocs(q);
        return querySnapshot.docs.map(doc => {
             const data = doc.data();
            return {
                id: parseInt(doc.id),
                ...data,
            } as Vehicle;
        });
    } catch (error) {
        console.error("Error fetching featured vehicles:", error);
        return [];
    }
}

export async function createVehicle(vehicleData: VehicleDataInput): Promise<Vehicle> {
    noStore();
    const q = query(collection(db, 'vehicles'), orderBy("id", "desc"), limit(1));
    const querySnapshot = await getDocs(q);
    const maxId = querySnapshot.empty ? 0 : parseInt(querySnapshot.docs[0].id);
    const newId = maxId + 1;
    
    const newVehicleData = { ...vehicleData, id: newId };
    
    const vehicleDocRef = doc(db, 'vehicles', newId.toString());
    
    const { id, ...dataToSave } = newVehicleData; 
    await setDoc(vehicleDocRef, dataToSave);
    
    return newVehicleData;
}

export async function updateVehicle(id: string | number, vehicleData: VehicleDataInput): Promise<Vehicle> {
    noStore();
    const vehicleDocRef = doc(db, 'vehicles', id.toString());
    await updateDoc(vehicleDocRef, vehicleData);
    
    const updatedDoc = await getDoc(vehicleDocRef);
    const data = updatedDoc.data();
    return {
        id: parseInt(id.toString()),
        ...data
    } as Vehicle;
}

export async function deleteVehicle(id: string | number): Promise<void> {
    noStore();
    const vehicleDocRef = doc(db, 'vehicles', id.toString());
    await deleteDoc(vehicleDocRef);
}


// --- Gallery Functions ---

export async function fetchGalleryImages(): Promise<GalleryImage[]> {
    noStore();
    const galleryCollectionRef = collection(db, 'gallery');
    try {
        const q = query(galleryCollectionRef, orderBy('description'));
        const querySnapshot = await getDocs(q);
        return querySnapshot.docs.map(doc => {
            const data = doc.data();
            return {
                id: doc.id,
                description: data.description,
                imageUrl: data.imageUrl,
                imageHint: data.imageHint,
            };
        });
    } catch (error: any) {
        if (error.code === 'permission-denied') {
            const permissionError = new FirestorePermissionError({
                path: galleryCollectionRef.path,
                operation: 'list',
            });
            errorEmitter.emit('permission-error', permissionError);
        }
        console.error("Error fetching gallery images:", error);
        throw error;
    }
}

export async function addGalleryImage(imageData: Omit<GalleryImage, 'id'>): Promise<GalleryImage> {
    noStore();
    const galleryCollectionRef = collection(db, 'gallery');
    try {
        const newDocRef = await addDoc(galleryCollectionRef, imageData);
        return {
            id: newDocRef.id,
            ...imageData,
        };
    } catch (error: any) {
        if (error.code === 'permission-denied') {
             const permissionError = new FirestorePermissionError({
                path: galleryCollectionRef.path,
                operation: 'create',
                requestResourceData: imageData
            });
            errorEmitter.emit('permission-error', permissionError);
        }
        console.error("Error adding gallery image:", error);
        throw error;
    }
}

export async function deleteGalleryImage(id: string): Promise<void> {
    noStore();
    const imageDocRef = doc(db, 'gallery', id);
    try {
        await deleteDoc(imageDocRef);
    } catch (error: any) {
        if (error.code === 'permission-denied') {
            const permissionError = new FirestorePermissionError({
                path: imageDocRef.path,
                operation: 'delete',
            });
            errorEmitter.emit('permission-error', permissionError);
        }
        console.error("Error deleting gallery image:", error);
        throw error;
    }
}
